.icon {
    margin-top: 0.25rem;
    width: 2rem;
    height: 2rem;
}

.icon:hover {
    color: #d3d3d3;
    cursor: pointer;
}

/* Add a highlight effect for the selected tool */
.icon.selected {
    color: #d3d3d3; /* Change this to your preferred highlight color */
    color: #d3d3d3;
}

/* Hovering over tools display text */
.icontiptext { visibility: hidden;
  padding: 0.25em 0.5em;
  background-color: salmon;
  color: #fff;
  text-align: center;
  position: absolute;
  z-index: 1;
}

.icon:hover .icontiptext{
  visibility: visible;
  transition-delay: 0.3s;
}

.editor {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.mobile {
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  text-align: center;
}

.book {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.colors {
  margin-top: 3em;
  border: 1px solid black;
}

.canvasContainer {
  display: flex;
  align-self:center;
  justify-content: center;
}

.toolbar {
  display: flex;
  background-color: var(--color-fourth);
  border: solid 0.25em black;
  flex-direction: column;
  align-items: center;
  align-self: center;
  justify-content: center;
  height: 75%;
}

.tools {
  width: 3em;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* align-items: center; */
}

