.size {
  display: flex;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.5);
  justify-content: center;
  align-items: center;
  height: 90vh;
  width: 90vw;
  overflow: scroll;
}

#canvas {
  display: block; 
  padding: 0;
  margin: 0; 
}

/* .colorPicker { */
/*   position: absolute; */
/*   z-index: 5; */
/*   top: 20rem; */
/*   left: 5rem; */
/* } */
.colorPicker {
  position: absolute;
  z-index: 5;
}

.colors {
  position: absolute; /* Ensure it's positioned absolutely */
  left: 3em; /* Position it immediately to the right of the toolbar */
  top: 0; /* Align it with the top of the toolbar */
  transform: translateX(150%) translateY(70%); /* Push it right next to the toolbar */
  z-index: 100; /* Ensure it appears on top of the canvas */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0.5em;
  width: 5em; /* Adjust the width of the color picker */
}
